* {
  font-family: "Montserrat", sans-serif;
  box-sizing: border-box;
  margin: 0;
}
h2 {
  font-weight: bold;
}
.form {
  margin-top: 30px;
  padding: 20px;
  border: 1px solid #ccc;
  display: grid;
  color: white;
  border-radius: 5px;
  background-color: white;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
}
.form button {
  height: 40px;
  font-weight: bolder;
  margin-left: 84%;
  margin-top: 40px;
}
.container {
  background-color: #f2f2f2;
  align-content: center;
  justify-content: center;
  padding: 10px;
  /* margin-top: 10px; */
  border-radius: 5px;
}
body {
  background-color: #f2f2f2;
}
.nav-link {
  color: white;
}
